#home {
  height: auto;

  .top-section {
    height: 100%;
    background: linear-gradient(180deg, #131313 0%, #18102c 100%);
    background-size: cover;
    position: relative;

    .discoLines {
      position: absolute;
      z-index: 1;
      pointer-events: none;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url('../../assets/img/discolines.png');
      background-size: cover;
      background-position: center center;
      opacity: 0.2;
    }

    .hero {
      padding: 2rem 5%;
      margin-top: -6rem;
      color: #fff;
      display: grid;
      height: 100vh;
      grid-template-columns: 100%;
      grid-template-rows: repeat(2, 1fr);
      align-items: center;

      h2 {
        font-size: 4rem;
        line-height: 1;
      }

      > * {
        justify-content: center;
      }

      .imgContainer {
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;

        img {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          object-fit: contain;
          aspect-ratio: 16 / 9;
        }
      }

      @media (min-width: $ipad) {
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: 100%;

        h2 {
          font-size: 6rem;
        }
      }
    }
  }

  .section {
    &.events {
      position: relative;
      height: 100%;
      min-height: 100vh;
      background: url('../../assets/img/Group\ 5673.png') center center
        no-repeat;
      background-size: cover;
      background-color: $black-200;
      padding: 2rem 5%;

      text-align: center;

      display: flex;
      flex-direction: column;
      justify-content: center;

      gap: 1rem;

      .cards {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 1rem;

        .card {
          background-color: rgba($white, 0.5);
          padding: 3rem 2rem;

          border-radius: 0.35rem;

          font-size: 1.35rem;
          font-weight: 700;
        }
      }
    }

    &.how-it-works {
      padding: 2rem 5%;
      background-image: url('../../assets/img/BG.jpg');

      .grid-layout {
        .imgContainer-1 {
          order: 2;
        }
      }
    }

    &.words-of-mouth {
      padding: 2rem 5%;
    }
  }

  .grid-layout {
    display: grid;
    grid-template-columns: 100%;
    gap: 1rem;
    align-items: center;

    .imgContainer {
      height: 100%;
    }
  }

  .intro {
    text-align: center;
    padding: 1rem;
    max-width: $laptop;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  @media (min-width: $ipad) {
    .section {
      &.events {
        .cards {
          grid-template-columns: repeat(4, 1fr);
          gap: 2rem;
        }
      }

      &.how-it-works {
        .grid-layout {
          .imgContainer-1 {
            
            order: initial;
          }
        }
      }
    }

    .grid-layout {
      grid-template-columns: repeat(2, 1fr);
      gap: 2rem;

      
    }
  }
}
