.header {
  display: flex;
  background-color: transparent;
  justify-content: space-between;
  padding: 2rem 5%;
  color: #fff;

  .logo {
    height: 4rem;

    img {
      height: 100%;
      width: 100%;
    }
  }

  .nav {
    display: flex;
    width: 100%;
    justify-content: center;

    .left {
      display: flex;
      width: 100%;
      justify-content: center;
      gap: 1rem;

      Link {
        display: block;

        &:hover {
          color: $red;
        }
      }
    }

    .right {
      margin-right: 0;

      button {
        background: linear-gradient(265.86deg, #d6882f 8.45%, #fbb961 101.33%);
      }
    }
  }

  @media (min-width: $ipad) {
    .nav {
      .left {
        gap: 2rem;
      }
    }
  }
}
