@import url('https://fonts.googleapis.com/css2?family=Barlow:wght@100;300;400;900&display=swap');
@import './utils/all';

html {
  height: 100%;
  width: 100%;
  font-size: 75%;

  @media (min-width: $laptop) {
    font-size: 85%;
  }

  @media (min-width: $large-screen) {
    font-size: 95%;
  }
}

* {
  font-family: inherit;
  transition: 0.3s ease-in-out;
  &,
  &::before,
  &::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    user-select: none;
  }
}

#root,
#app {
  height: 100%;
  position: relative;
  z-index: 1000;
}

body {
  height: 100%;
  width: 100%;
  background: #000;
  color: #fff;
  transition: all 0.05s linear;
  line-height: 2.1rem;
  font-family: 'inter';
  font-style: normal;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a,
Link {
  text-decoration: none;
  color: inherit;
}

button {
  outline: none;
  border: none;
}

img {
  height: 100%;
  width: 100%;
}

main{
  min-height: 100%;
}

@import './components/all';
@import './layouts/all';
@import './pages/all';
