.footer {
  position: relative;
  display: grid;
  grid-template-columns: 100%;
  padding: 2rem 5%;
  gap: 1rem;
  align-items: flex-end;
  line-height: 1.6;

  .button {
    background-color: $purple;
    color: $white;
    width: 100%;
    justify-content: center;
  }

  .social-media {
    display: flex;
    gap: 1rem;
    justify-content: center;
    align-items: center;
  }

  .tab-1 {
    display: flex;
    flex-direction: column;
    height: 100%;
    align-items: center;
    justify-content: center;
    p {
      color: $white-200;
    }
  }

  .tab-2 {
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 0.65rem;
    line-height: 1.6;

    .hide-mobile {
      color: $white-200;
    }

    .user-permission {
      display: grid;
      white-space: nowrap;
      grid-template-columns: 100%;
      gap: 1rem;
    }
  }

  .tab-3 {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;

    p {
      margin-bottom: 0.25rem;

      &:last-of-type {
        margin-top: 1rem;
      }
    }

    .download-platforms {
      img {
        font-size: 2rem;
      }
  }

 
  }

  @media (min-width: $ipad) {
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;

    .tab-1 {
      align-items: flex-start;
    }

    .tab-2 {
      .user-permission {
        grid-template-columns: repeat(3, 1fr);
      }
    }

    .tab-3 {
      align-items: flex-end;
      justify-content: flex-end;
    }
  }
}
